import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home.vue'
import Login from '@/components/Login.vue'
import User from '@/components/user/user.vue'
import Personal from '@/components/user/personal.vue'
import Password from '@/components/user/password.vue'
import Check from '@/components/check/check.vue'
import Checkday from '@/components/check/checkday.vue'
import Checkmonth from '@/components/check/checkmonth.vue'
import Holiday from '@/components/check/holiday.vue'
import Attendance from '@/components/check/attendance.vue'
import Achievements from '@/components/achievements/achievements.vue'
import Work from '@/components/work/work.vue'
import Birthday from '@/components/work/birthday.vue'
import Batch from '@/components/work/batch.vue'
import Price from '@/components/price/price.vue'
import Pricesets from '@/components/price/pricesets.vue'
import Depart from '@/components/post/department.vue'
import Post from '@/components/post/post.vue'
import Message from '@/components/talk/message.vue'
import {getCookie} from '../cookie'

Vue.use(Router)

const routes =[
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'User',component:User,name:''},
      {path:'Personal',component:Personal,name:''},
      {path:'Password',component:Password,name:''},
    ]
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'Check',component:Check,name:''},
      {path:'Checkday',component:Checkday,name:''},
      {path:'Checkmonth',component:Checkmonth,name:''},
      {path:'Holiday',component:Holiday,name:''},
      {path:'Attendance',component:Attendance,name:''},
    ]
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'Achievements',component:Achievements,name:''},
    ]
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'Work',component:Work,name:''},
      {path:'Birthday',component:Birthday,name:''},
      {path:'Batch',component:Batch,name:''},
    ]
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'Price',component:Price,name:''},
      {path:'Pricesets',component:Pricesets,name:''},
    ]
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'Depart',component:Depart,name:''},
      {path:'Post',component:Post,name:''},
    ]
  },
  {
    path: '/Home',
    component: Home,
    meta: {
      requireAuth: true, // 
    },
    name:'',
    children:[
      {path:'Message',component:Message,name:''},
    ]
  },
  {path:'*',redirect:'/Home/User'}
]

const router = new Router({
  routes
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(r => r.meta.requireAuth)) {
    if (getCookie('account')) {      //  cookie 
      next();
    }
    else {
      next({
        path: '/',
        query: {redirect: to.fullPath}
      })
    }
  }
  else {
    next();
  }
})

export default router;



